@import url(https://fonts.googleapis.com/css2?family=Architects+Daughter&family=Caveat&family=Cormorant+Upright&family=Fuzzy+Bubbles&family=Indie+Flower&family=Kranky&family=Lobster+Two&family=Patrick+Hand&family=Shadows+Into+Light+Two&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Oswald:wght@700&family=Tangerine:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.rectangle-container{
    display: flex;
    justify-content: center;
    align-items: center;

}

.rectangle-picture{
    transition:0.4s;
    /* height: max-content; */
}

.rectangle-picture:hover {
    transform: scale(1.2); 
  }


  @media only screen and (max-width: 700px) {
    .rectangle-picture{
        height: auto;
    }

  } 
.card{
    background-color: #BF3C1F;
}

.card h2{
    font-family: Oswald;
    font-style: normal;
    font-weight: bold;
    font-size: 1.8rem;
    margin: 0.8rem 0rem;
    text-align: center;
    padding: 0.5rem;
    color: #204465;
}
.pop-up-div{
    background-color: white;
    height: 75%;
    width: 70%;
    display: grid;
    grid-template-columns: 50% 50%;
    /* padding: 1.5rem; */
}

.pop-up-container-left{
    padding: 1.5rem;
}

.pop-up-container-right{
    padding: 1rem;
    padding-right: 3.5rem;
    overflow-y: scroll;
}
.pop-up-flex-box{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    height: 100%;

}

.pop-up-flex-column{
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 30vh;
    
}

.pop-up-grid{
    display: grid;
    grid-template-rows: 70% 15% 15%;
    justify-items: center;
}

.pop-up-statement-text{
    font-family: 'Open Sans';
}

.pop-up-by-embroiderer-text{
    font-family: 'Open Sans';
    font-weight: bold;
    margin-top: 2rem;

}

.main-text {
    font-size: 1.1em;
}

.translation {
    color:maroon;
    font-weight: 700;
    font-size: 1.2em;
}

@media only screen and (max-width: 700px) {
    .pop-up-div{
        height: 90%;
        width: 70%;
        display: flex;
        flex-direction: column;
        overflow-y: scroll;
    }
    .pop-up-container-right{
        overflow-y: visible;
        overflow-y: initial;
    }
    .pop-up-container-right{
        padding: 0 2rem 2rem 2rem;
    }

    .pop-up-container-left{
        padding: 1rem 1rem 0 1rem;
    }
    .pop-up-flex-column{
        height: 100%;
    }

    .pop-up-img{
        width: 100% !important;
        height: auto !important;
    }

  } 

.home-page-div {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background:#151B26;
    z-index: 1;
    display: flex;
    justify-content: center;
}

.home-page-container {
  width: 60%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* align-items: center; */
  margin-top: 2rem;
  
}

.main-page-title {
  color: white;
  font-size: 2.5rem;
  font-family: Oswald;
  text-align: center;
}

.main-page-text {
  color: white;
  /* background-color: lightcoral; */
  text-align: center;
  max-height: 25%;
  overflow-y: scroll;
}

/* Hide scrollbar for Chrome, Safari and Opera */
/* .main-page-text::-webkit-scrollbar {
  display: none;
} */

/* Hide scrollbar for IE, Edge and Firefox */
/* .main-page-text {
-ms-overflow-style: none;  
scrollbar-width: none;  
} */

.read-more-link {
  color: white;
}

.read-more-text {
  display: none;
}

.read-more-text--show {
  display: inline;
}

.read-more-btn {
  color: black;
}

.main-page-button {
  background-color: #BF3C1F;
  color: white;
  padding: 1rem;
  border-radius: 12px;
  font-weight: bold;
  margin-top: 2rem;
  width: 12rem;

}

.main-page-pics {
  display: grid;
  grid-template-columns: 50% 50%;
  justify-items: center;
  grid-gap: 1rem;
}

.button-div{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
html {
  overflow-y:scroll;
}

.body{
  margin: 0;
  padding: 0;
  overflow-y: scroll;
  
}

.home-div{
  background-color: #151B26;
  display:flex;
  min-height: 100vh;
  justify-content: center;
  
}

.main-container{
  width: 90%;

}
.main-title{
font-family: 'Tangerine';
font-style: normal;
font-weight: bold;
font-size: 3rem;
text-align: center;
letter-spacing: 0.1em;
color: #1F638C;
}

.main-title-div{
  background-color: #F6F1F1;
  height: 4rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  border-radius: 40px;
  margin-bottom: 1rem;

}

.blue-line{
  margin-top: 1rem;
  margin-bottom: 0.5rem;
  border-radius: 40px;
  height: 5px;
  background-color: #03658C;
}

.top-container{
  display: grid;
  /* grid-template-columns: 14% 14% 14% 14% 14% 14% 14% 14% 14% ; */
  grid-template-columns: repeat(9, auto);
}

.bottom-container{
  display: grid;
  grid-template-columns: 40% 20% 40%;

}

.middle-container {
  display: grid;
  grid-template-rows: 20% auto 60%;
}

.pop-up-container{
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(255, 255, 255, 0.52);
  display: flex;
  justify-content: center;
  align-items: center;
}

.stairs-container{
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.stair-step{
  background-color: #03658C ;
}

.blue-line-bottom{
  margin-top: 0.3rem;
  margin-bottom: 0.5rem;
  border-radius: 40px;
  height: 5px;
  background-color: #03658C;
}

.vertical-blue-line{
  background-color: #03658C;
  width: 0.3rem;
}

.main-container-grid{
  display: grid;
  grid-template-columns: 25% 1% 48% 1% 25%;

}

.left-container{
  display: grid;
  grid-template-columns: 50% 50%;
  justify-items: center;
}

.right-container{
  display: grid;
  grid-template-columns: 50% 50%;
  justify-items: center;
}

.first-row-middle-container{
  display: grid;
  grid-template-columns: repeat(6, auto);
}

.second-row-middle-container{
  display: flex;
  justify-content: space-between;
  position: relative;
  height: inherit;
}

.windows-div {
  display: flex;
  position: relative;
  width: 50%;
  height: 100%;
  justify-content: center;
  align-items: center;
}

.windows {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: #1F638C;
  position: relative;
  border-radius: 45% 45% 0px 0px; 
  width: 65%;
  height: 85%;
}

.windows-top-panes {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  position: relative;
  width: 100%;
  height: 50%;
}

.windows-top-panes-rectangles-left {
  background: #BF3C1F;
  position: absolute;
  border-radius: 80% 0px 0px 0px;
  bottom: 11%;
  left: 19%;
  width: 24%;
  height: 63%; 
}

.windows-top-panes-rectangles-right {
  background: #BF3C1F;
  position: absolute;
  border-radius: 0px 80% 0px 0px;
  bottom: 11%;
  right: 19%;
  width: 24%;
  height: 63%; 
}

.windows-bottom-panes {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 100%;
  height: 50%;
  position: relative;
}

.windows-bottom-panes-rectangles-left {
  background: #BF3C1F;
  position: absolute;
  bottom: 19%;
  left: 19%;
  width: 24%;
  height: 75%; 
}

.windows-bottom-panes-rectangles-right {
  background: #BF3C1F;
  position: absolute;
  bottom: 19%;
  right: 19%;
  width: 24%;
  height: 75%; 
}

.second-sub-row-middle-container{
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-items: center;
}

.third-row-middle-container{
  display: grid;
  grid-template-columns: 36% 28% 36%;

}

.first-sub-row-middle-container{
  display: grid;
  align-items: center;
}

.third-sub-row-middle-container{
  display: grid;
  align-items: center;
}

.first-sub-row-flex{
  display: flex;
  justify-content: space-between;

}

.second-sub-row-flex{
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.line {
  width: 100%;
  height: 1px;
  position: absolute;
  border-bottom: 1px solid black;
}

.rectangle-for-diamond {
  display: flex;
  align-items: center;
  position: relative;
  width: 97%;
  height: 100%;
  box-sizing: border-box;
  background: #BF3C1F;
}

.diamonds-div {
  display: flex;
  width: 100%;
  height: 100%;
  position: absolute;
  justify-content: space-evenly;
  align-items: center;
}

.diamonds {
  width: 5%;
  height: 60%;
  background: #1F638C;
  transform: rotate(-45deg);
}

.main-container-phone{
  display: flex;
  justify-content: center;
  background-color: #151B26;
}

.main-sub-container-phone{
  width: 90%;
  display: flex;
  flex-direction: column;
  justify-items: center;

}

.first-container-grid-phone{
  display: grid;
  grid-template-columns: 50% 50%;
  grid-row-gap: 3px;
  justify-items: center;
  align-items: center;
}

.pictures-with-windows-phone {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.rectangle-for-diamond-phone {
  display: flex;
  align-items: center;
  position: relative;
  width: 97%;
  height: 2.25rem;
  box-sizing: border-box;
  background: #BF3C1F;
  margin: 1rem 0rem;
}



@media only screen and (max-width: 700px) {
.main-title-div{
 margin-top: 2rem;
 grid-template-columns: 50% 50%;
  }

.main-title{
  font-size: 2rem;

}
}
